import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VisitorListComponent } from './visitor-list.component';
import { VisitorListRoutingModule } from './visitor-list-routing.module';
import { MatInputModule, MatFormFieldModule, MatTableModule, MatPaginatorModule,
  MatSortModule, MatCardModule, MatCheckboxModule, MatTooltipModule, MatButtonModule, MatDialogModule, MatSlideToggleModule} from '@angular/material';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {ToasterModule } from 'angular5-toaster';

@NgModule({
  imports: [
    VisitorListRoutingModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatCardModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonModule,
    MatPaginatorModule,
    MatDialogModule,
    MatTableModule,
    MatSortModule,
    MatTooltipModule,
    ToasterModule,
    MatSlideToggleModule
  ],
  declarations: [VisitorListComponent]
})
export class VisitorListModule { }



